@font-face {
  font-family: "Peace-Sans";   /*Can be any text*/
  src: local("Peace Sans Webfont"),
    url("./assets/fonts/Peace Sans Webfont.ttf") format("truetype");
}

@font-face {
  font-family: "Pilkius-Romeus";   /*Can be any text*/
  src: local("Pilkius Romeus"),
    url("./assets/fonts/PilkiusRomeus-YwDL.ttf") format("truetype");
}


* {
	box-sizing: border-box;
}

html {
	margin: 0;
}

body {
	margin: 0;
	font-family: 'Pilkius-Romeus', sans-serif;
	color: #fff;
    overflow: hidden;
    height: 100vh;
    max-height: 100vh;
}


main {
    overflow: hidden;
    background: #d76ee7;
	width: 100%;
}

.main-background-wrapper {
	background:  url("./assets/images/background.svg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: bottom;
    width: 100%;
    height: 100vh;
	margin: auto 0;
	padding: 40px 0;
	position: relative;
}

.bootscreen--top-text {
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    margin-top: 3%;
    font-size: 22px;
    position: absolute;
    left: 0;
    right: 0;
    z-index: 3;

}

.bootscreen--top-bubble-logo {
    width: auto;
    height: 85px;
    display: block;
    margin: auto;
    margin-top: 40px;
}

.bootscreen--top-bubble-hero-text {
    font-family: "Peace-sans";
    font-size: 30px;
    font-weight: 100;
    margin-top: 0px;
}


.bootscreen--bottom {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    z-index: 1;
}

.bootscreen--bottom-img {
    height: 390px;
    width: auto;
    margin:auto;
    display: block;
}

.bootscreen--bottom-inner {
    position: relative;
}

.bootscreen--bottom-inner-text {
    position: absolute;
    bottom: 10px;
    left: 0;
    right: 0;
    text-align: center;
    width: 100%;
    z-index: 3;
}

.category--top-text {
    font-weight: 100;
    font-size:22px;
    font-family: "Peace-sans";
}

.category-screen {
    margin:auto;
    margin: auto;
    display: grid;
    margin-top: -15px;
    transform: translateX(3%);
}

.category-screen > a {
        transform: translateX(3%);
}

.category--options {
    display: inline-block;
    position: relative;
    width: 145px;
}

.category--options > img {
    width: 100%;
}

.category--options > div {
    color: #000;
}

.centered_text {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.quiz-screen--top-bar {
    display: flex;
    height: 45px;
    max-height: 45px;
    width: 660px;
    justify-content: space-around;
    align-items: center;
    margin:auto;
}

.quiz-screen--back-arrow {
    height: 45px;
    cursor: pointer;
}

.quiz-screen--top-bar-title {
    font-size: 20px;
    font-family: Peace-sans;
    font-weight: 100;
    text-transform: uppercase;
}

.quiz-screen--question-area {
    width: 650px;
    margin: auto;
}

.quiz-screen--question {
    margin:auto;
    width: 75%;
    margin-top: 25px;
    height: 80px;
}
.quiz-screen--question > h1 {
    font-weight: 100;
    text-transform: uppercase;
    font-size: 1.05em;
    text-align: center;
}

.quiz-screen--option-area {
    margin:auto;
    width: 650px;
    display: grid;
    justify-content: center;
    margin-top: 29px;
    transform: translate(-5%, 5%);
    z-index: 9;
    position: relative;
}

.quiz-screen--wrapper {
    position: relative;
    width: 100%;
    text-align: center;
}

.quiz-screen--option-text {
    position: absolute;
    top: 12%;
    left: 0%;
    color: #000;
    width: 100%;
    transform: scale(0.85);
}

.quiz-screen--option-image {
    width: 195px;
}

.quiz-screen--yes-no-img-wrapper {
    position: absolute !important;
    bottom: -5px;
    z-index: 100;
}


.quiz-screen--yes-no-img {
    margin: auto;
    width: auto;
    height: 430px;
}

@media only screen and (min-width: 601px) {
    .quiz-screen--question > h1 {
            font-size: 1.17em ;
    }   
    .quiz-screen--option-area {
        margin-top: 20px;
    }
}
@media only screen and (max-width: 600px) {
	main {
		width: 100% !important;
	}

        
    .quiz-screen--yes-no-img-wrapper {
        left: 0;
    }
    .main-background-wrapper {
		width: 100% !important;
		padding: 10px 15px;
		padding-bottom: 40px;
		margin-top: 0vh;
        background-size: contain;
    }

    .category-screen > a {
        transform: translateX(15%);
    }

    .category-screen {
        margin-top: 40px;
    }

    .quiz-screen--question {
        margin-top: 10px;
    }

    .quiz-screen--option-area {
        transform: translate(-10%, 0px);
        margin-top: 45px;
    }

    .quiz-screen--option-area, .quiz-screen--top-bar, .quiz-screen--question-area {
        width: 100%;
    }

    .quiz-screen--question {
        width: 100%;
    }

    .bootscreen--bottom-img {
        height: auto;
        width: 100%;
    }

    .quiz-screen--option-image {
            width: 190px;
        }

    .quiz-screen--option-text {
        top: 0;
        bottom: 0;
        left: 0%;
        width: 100%;
        transform: scale(0.8);
        font-size: 20px;
    }

    .bootscreen--bottom {
    }
}

.start_btn {
    font-family: 'Pilkius-Romeus', sans-serif;
    padding: 15px;
    background: #d76ee7;
    border: 1px solid #fff;
    box-shadow: 0px 1px 9px 0px #ccc;
    color: #fff;
    width: 250px;
    cursor: pointer;
}
